
.slider {
  position: relative;
  background-color: #808080;
  width: 100%;
  height: 140px;
  overflow: hidden;
}
.container {
  position: absolute;
  list-style: none;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  height: 100%;
}
.container li {
  margin: 0;
  padding: 0;
  display: inline-block;
  height: 100%;
}
.container li img {
  display: block;
  width: 100%;
  height: 100%;
}
.dots {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  right: 40px;
  bottom: 20px;
  z-index: 10000;
}
.dots div {
  width: 9px;
  height: 9px;
  border-radius: 50%;
  border: 1px solid #808080;
  margin-left: 10px;
}
.dots .active {
  background-color: #fff;
}


